import React from 'react'

import i18n from '../../controller/i18n'
import Settings from '../../controller/Settings'

import Icon from '../../components/Icon'
import NotificationList from './List'

import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'
import Drawer from '@material-ui/core/SwipeableDrawer'
import Typography from '@material-ui/core/Typography'

import ChevronRightIcon from '@material-ui/icons/ChevronRight'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	drawer: styles => ({
		width: styles.isMobile ? '100%' : 500,
		backgroundColor: theme.palette.background.default
	}),
	drawerHeader: {
		display: 'flex',
		padding: theme.spacing(0, 1)
	},
	drawerIcon: {
		marginRight: theme.spacing()
	},
	drawerTitle: {
		margin: 'auto 0'
	},
	drawerClose: {
		marginLeft: 'auto'
	}
}));


i18n.setTranslations(language => require('./i18n/' + language));

export default function NotificationDrawer(props) {
	const { navigate } = props;
	const classes = useStyles({ isMobile: Settings.get('_isMobile') });
	
	const handleDrawerClose = () => navigate(null);
	
	return (
		<Drawer
			anchor='right'
			open={props.open}
			onOpen={console.log}
			onClose={handleDrawerClose}
			classes={{
				paper: classes.drawer
			}}
			>
			
			<div className={classes.drawerHeader}>
				<Typography variant='h6' component='h2' classes={{ root: classes.drawerTitle }}>
					<Icon icon='mdi-bell' color='inherit' className={classes.drawerIcon} />
					{i18n.t('Notifications')}
				</Typography>
				
				<IconButton classes={{ root: classes.drawerClose }} onClick={handleDrawerClose}>
					<ChevronRightIcon />
				</IconButton>
			</div>
			<Divider />
			
			<NotificationList {...props} />
		</Drawer>
	);
}
