import React, { useState, useEffect } from 'react'
import clsx from 'clsx'

import i18n from '../../controller/i18n'
import Notifications from '../../controller/Notifications'

import Divider from '../../components/Divider'
import Icon from '../../components/Icon'

import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'

import DeleteIcon from '@material-ui/icons/Delete'
import ArchiveIcon from '@material-ui/icons/Archive'
import RestoreIcon from '@material-ui/icons/Unarchive'
import ReadIcon from '@material-ui/icons/Drafts'
import UnreadIcon from '@material-ui/icons/Markunread'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	List: {
		padding: theme.spacing(3, 1.5)
	},
	unread: {
		backgroundColor: theme.palette.background.paper
	},
	read: {
		//backgroundColor: theme.palette.grey[300],
		opacity: 0.5
	},
	archived: {
		opacity: 0.5,
		fontStyle: 'italic'
	},
	bold: {
		fontWeight: 'bold',
	}
}));


export default React.memo(props => {
	const classes = useStyles();
	const [notifications,setNotifications] = useState([]);
	
	const NotificationsClass = new Notifications();
	const showNotification = notification => {
		NotificationsClass.emit('new', { ...notification, 'display': 'popup', 'autoHideDuration': null });
	}
	
	useEffect(() => {
		const parse = res => setNotifications([ ...res ].reverse());
		
		NotificationsClass.get('all', parse);
		NotificationsClass.on('all', parse);
		
		return () => NotificationsClass.off('all', parse);
		
	}, [NotificationsClass]);
	
	// get notifications
	let notificationElements = [];
	let archivedNotificationElements = [];
	
	notifications.forEach(notification => {
		if (notification.state !== 'archived') {
			notificationElements.push(<Notification key={notification.id} onClick={() => showNotification(notification)} {...notification} />);
		}
		else {
			archivedNotificationElements.push(<Notification key={notification.id} onClick={() => showNotification(notification)} {...notification} />);
		}
	});
	
	return (
		<List classes={{ root: classes.List }} dense>
			<NotificationList list={notificationElements} />
			<NotificationList list={archivedNotificationElements} hidden divider={archivedNotificationElements.length > 0 && ((hidden) => <em>{i18n.t((hidden ? 'show' : 'hide') + ' archived Notifications')}</em>)}
				/>
			
		</List>
	);
	
}, (prevProps, nextProps) => {
	return nextProps.open === false; // do not update when drawer is closed
});


function NotificationList(props) {
	const { list, divider } = props;
	
	const [hidden,setHidden] = useState(props.hidden !== undefined ? props.hidden : false);
	
	return (
		<>
			{divider && <ListItem dense button onClick={() => setHidden(prevHidden => !prevHidden)}>
				<Divider>{divider(hidden)}</Divider>
			</ListItem>}
			{hidden !== true && list}
		</>
	);
}


function Notification(props) {
	const { id, state, onClick, title = null, message = '', icon, iconColor } = props;
	const classes = useStyles();
	
	if (title === null) {
		return null;
	}
	
	const set = s => {
		const NotificationsClass = new Notifications();
		
		if (s === 'delete') {
			NotificationsClass.del(id);
		}
		else {
			NotificationsClass.set(id, { 'state': s });
		}
	}
	
	return (
		<ListItem
			onClick={onClick}
			classes={{ root: clsx(state === 'read' && classes.read, (!state || state === 'unread') && classes.unread, state === 'archived' && classes.archived) }}
			dense
			divider
			button
			>
			
			<ListItemIcon>
				<Icon icon={icon || 'mdi-message'} color={iconColor} />
			</ListItemIcon>
			<ListItemText
				classes={{ primary: clsx((!state || state === 'unread') && classes.bold), secondary: clsx((!state || state === 'unread') && classes.bold) }}
				primary={title}
				secondary={message.length > 40 ? message.substr(0,35) + '...' : message}
				/>
			
			<ListItemSecondaryAction>
				{state !== 'archived' ? (
				<IconButton onClick={() => set(state === 'unread' ? 'read' : 'unread')} edge="end" aria-label={state === 'unread' ? 'read' : 'unread'}>
					{state === 'unread' ? <UnreadIcon size="small" /> : <ReadIcon size="small" />}
				</IconButton>) : (
				<IconButton onClick={() => set('read')} edge="end" aria-label={'restore'}>
					<RestoreIcon size="small" />
				</IconButton>)}
				
				<IconButton onClick={() => set(state === 'archived' ? 'delete' : 'archived')} edge="end" aria-label={state === 'archived' ? 'delete' : 'archive'}>
					{state === 'archived' ? <DeleteIcon size="small" /> : <ArchiveIcon size="small" />}
				</IconButton>
			</ListItemSecondaryAction>
		</ListItem>
	);
}